@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Poppins',
    sans-serif;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  background-color: #F2F2F2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{display:flex;justify-content:center;background-color:white;padding:10px 15px 10px 15px}.navbar .container{display:flex;justify-content:space-between;align-items:center;width:100%}.navbar .container .logo{flex-grow:1}.navbar .container .logo img{height:40px}.navbar .container .navigation{display:flex;justify-content:flex-end;flex-grow:1;grid-gap:5%;gap:5%}.navbar .container .navigation .nav-item{padding:5px 20px 5px 20px;color:#a7a7a7;border-radius:0.7rem;font-weight:bold;transition:all 0.2s linear}.navbar .container .navigation .nav-item:hover{background-color:#e9e9e9;color:#616161;transition:all 0.2s linear}@media (min-width: 576px){.navbar .container{width:80%}}@media (min-width: 768px){.navbar .container .logo{flex-grow:2}.navbar .container .navigation{flex-grow:1}}

.hero{display:flex;justify-content:center;height:100vmin;background-color:#222EA2;padding:80px 15px 10px 15px}.hero .main-title{color:white;font-size:25px;font-weight:bold;text-align:center}@media (min-width: 768px){.hero .main-title{font-size:40px}}@media (min-width: 992px){.hero .main-title{font-size:45px}}@media (min-width: 1200px){.hero .main-title{font-size:50px}}

.menu{display:flex;justify-content:center;padding:50px 20px 50px 20px}.menu .container{display:grid;grid-template-columns:1fr;grid-row-gap:20px;row-gap:20px;width:100%}@media (min-width: 576px){.menu .container{grid-template-columns:1fr 1fr;grid-gap:20px 20px;gap:20px 20px}}@media (min-width: 992px){.menu .container{width:80%}}

.menu-card{display:flex;min-height:100px;width:100%;border-radius:0.8em;background-color:white;color:#222EA2;transition:box-shadow 0.2s linear}.menu-card .icon{display:flex;align-items:center;padding-left:20px;min-width:100px;font-size:45px}.menu-card .menu-name{display:flex;font-size:20px;font-weight:700;align-items:center}.menu-card:hover{box-shadow:4px 4px 8px rgba(0,0,0,0.15);transition:box-shadow 0.2s linear}

iframe.practicum-video{margin:0px;padding:0px}

.practicum-video{padding-bottom:30px;display:grid;grid-template-areas:"player" "sidebar";grid-template-rows:0fr 1fr;grid-gap:20px;gap:20px}.practicum-video .sidebar{display:grid;grid-template-columns:1fr;grid-gap:20px;gap:20px;padding:0px 20px 0px 20px}.practicum-video .sidebar .lang-switch{display:flex;background-color:white;border-radius:2em;min-height:40px;font-weight:bold;color:#727272}.practicum-video .sidebar .lang-switch .lang1{width:50%;display:flex;justify-content:center;align-items:center;border-radius:inherit;cursor:pointer}.practicum-video .sidebar .lang-switch .lang2{width:50%;display:flex;justify-content:center;align-items:center;border-radius:inherit;cursor:pointer}.practicum-video .sidebar .lang-switch .active{background-color:#222EA2;color:white}.practicum-video .sidebar .other-video-card{display:flex;color:#222fa283;border-radius:0.8em;background-color:white;min-height:75px;font-size:14px;transition:all 0.2s linear;cursor:pointer}.practicum-video .sidebar .other-video-card .icon{display:flex;align-items:center;justify-content:center;min-width:90px;font-size:36px}.practicum-video .sidebar .other-video-card .video-name{display:flex;align-items:center;font-weight:bold;padding-right:20px}.practicum-video .sidebar .other-video-card:hover{box-shadow:4px 4px 4px rgba(0,0,0,0.15);transition:all 0.2s linear}.sticky{position:fixed;top:0;width:100%}@media (min-width: 992px){.practicum-video{padding:20px 20px 40px 20px;grid-template-areas:"player sidebar";grid-template-columns:0.7fr 0.3fr;grid-template-rows:1fr}.practicum-video .player{position:static}.practicum-video .sidebar{padding:0px}}

.journal-cover{display:flex;justify-content:center;align-items:center;flex-direction:column;padding:30px 20px 50px 20px;color:#222EA2}.journal-cover .container{display:flex;flex-direction:column;grid-gap:20px;gap:20px}.journal-cover .container .title{font-weight:bolder;font-size:1.5rem;color:#727272;text-align:center}.journal-cover .container .jc-card-list{display:grid;grid-template-columns:1fr;grid-gap:20px;gap:20px}.journal-cover .container .jc-card-list a{text-decoration:none;color:#222EA2}.journal-cover .container .jc-card-list .jc-card{display:flex;border-radius:0.8em;background-color:white;min-height:75px;font-size:14px;transition:all 0.2s linear;cursor:pointer}.journal-cover .container .jc-card-list .jc-card .icon{display:flex;align-items:center;justify-content:center;min-width:90px;font-size:36px}.journal-cover .container .jc-card-list .jc-card .module-name{display:flex;align-items:center;font-weight:bold;padding-right:20px}.journal-cover .container .jc-card-list .jc-card:hover{box-shadow:4px 4px 4px rgba(0,0,0,0.15);transition:all 0.2s linear}@media (min-width: 992px){.journal-cover .container{width:80%;grid-gap:30px;gap:30px}.journal-cover .container .title{font-size:2.5em}.journal-cover .container .jc-card-list{grid-template-columns:repeat(2, 1fr)}.journal-cover .container .jc-card-list .jc-card{font-size:16px}}

.assistants{display:flex;justify-content:center;align-items:center;flex-direction:column;padding:30px 20px 50px 20px;color:#222EA2}.assistants .container{display:flex;flex-direction:column;grid-gap:20px;gap:20px}.assistants .container .title{font-weight:bolder;font-size:1.5rem;color:#727272;text-align:center}.assistants .container .assistants-list{display:grid;grid-template-columns:1fr 1fr;grid-gap:10px;gap:10px}.assistants .container .assistants-list .assistant-card{background-color:white;border-radius:0.5em;padding:10px 20px 10px 20px;font-size:12px;transition:all 0.2s linear}.assistants .container .assistants-list .assistant-card .assistant-code{font-weight:bold}.assistants .container .assistants-list .assistant-card:hover{box-shadow:2px 2px 2px rgba(0,0,0,0.15);transition:all 0.2s linear}@media (min-width: 768px){.assistants .container{width:80%}.assistants .container .assistants-list{grid-template-columns:repeat(3, 1fr)}}@media (min-width: 992px){.assistants .container{grid-gap:30px;gap:30px}.assistants .container .title{font-size:2.5rem}.assistants .container .assistants-list .assistant-card{font-size:16px}}

.handouts{padding:20px 20px 40px 20px}.handouts .container{display:flex;flex-direction:column;grid-gap:30px;gap:30px}.handouts .container .main-title{font-weight:bolder;font-size:1.5rem;color:#727272;text-align:center}.handouts .container .handouts-list{display:grid;grid-template-columns:1fr;grid-gap:30px;gap:30px}@media (min-width: 768px){.handouts .container{align-items:center}.handouts .container .main-title{font-size:2.5rem}.handouts .container .handouts-list{width:80%;grid-template-columns:1fr 1fr}}

.handout-card{background-color:white;border-radius:0.8em;padding:20px 20px 20px 20px;color:#222EA2;transition:all 0.2s linear}.handout-card .handout-info .title{font-weight:bold}.handout-card:hover{box-shadow:4px 4px 4px rgba(0,0,0,0.15);transition:all 0.2s linear}@media (min-width: 768px){.handout-card .handout-info .title{font-size:1.2rem}}

.organigram{padding:20px 20px 40px 20px}.organigram .container{display:flex;flex-direction:column;align-items:center;margin:auto;grid-gap:20px;gap:20px}.organigram .container .title{font-weight:bolder;font-size:1.5rem;color:#727272;text-align:center}.organigram .container .organigram-img{width:100%;border-radius:0.8em}@media (min-width: 992px){.organigram .container{width:80%}.organigram .container .title{font-size:2.5rem}}

.footer{display:flex;justify-content:center;background-color:#242424;color:white;padding:50px 20px 5px 20px}.footer .container{display:flex;flex-direction:column;grid-gap:40px;gap:40px}.footer .container .section1 .title{font-size:22px;font-weight:bold}.footer .container .section1 .socmed{display:flex;font-size:25px;margin-top:15px;grid-gap:30px;gap:30px}.footer .container .section2 .title{font-size:18px;font-weight:medium}.footer .container .section2 .headquarter .address{font-weight:normal;color:#7a7a7a}.footer .container .section3 .title{font-size:18px;font-weight:medium}.footer .container .section3 .explore .footer-menu-list{font-weight:normal;color:#7a7a7a;display:flex;flex-direction:column;grid-gap:20px;gap:20px}.footer .container .back-to-top-icon{font-size:30px;align-self:center;opacity:0.5;cursor:pointer}.footer .container .back-to-top-icon:hover{opacity:1;transition:all 0.2s linear}.footer .container .credits{color:#7B7B7B;font-size:12px;text-align:center}@media (min-width: 768px){.footer .container{width:80%}.footer .container .section1 .title{font-size:25px}.footer .container .section2 .find-us{width:75%}}

.not-found{min-height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.not-found .error-message{font-size:1.5rem;color:#464646;display:flex;flex-direction:column;align-items:center;grid-gap:20px;gap:20px;padding:0 40px 0 40px}.not-found .error-message img.img-404{width:100%}.not-found .error-message .messages{opacity:0.5;font-size:1.5rem;text-align:center}@media (min-width: 576px){.not-found .error-message{flex-direction:row}.not-found .error-message img.img-404{width:50%}.not-found .error-message .messages{font-weight:bold}}@media (min-width: 992px){.not-found .error-message{width:70%}.not-found .error-message .messages{font-size:2rem}}

